// global variable
// ***** color ***** //
$primary-color: #3171e5;
$secondary-color: #185abc;
$white-color: #fff;
$desc-color: #4b5371;
$heading-black: #25396f;
$body-color: #f8fafc;

// global css
#wpwrap {
	background: $body-color;
}
.rv_dash-container {
	max-width: 1240px;
	margin: auto;
	padding: 0px 16px;

	@media (max-width: 768px) {
		max-width: 98%;
	}
}

.rv_dash-btn{
    text-decoration: none;
    display: inline-flex;
    padding: 12px 24px;
    font-size: 14px;
    font-weight: 500;
    border-radius: 35px;
    letter-spacing: 0.2px;
    border: none;
    transition: 0.3s;
    &.btn-has-svg{
        display: inline-flex;
        align-items: center;
        gap: 4px;
        text-underline-offset: 3px;
        color: $primary-color;
        &:hover{
            text-decoration: underline;
        }
        svg{
            width: 12px;
            fill: $primary-color;
        }
    }

}
.rv-btn-filled {
	background-color: $primary-color;
	color: $white-color;
	border: 1px solid $primary-color !important;
	margin-right: 16px;
	&:hover {
		background-color: $secondary-color;
	}
}
.rv-btn-unfilled {
	background-color: transparent;
	color: $primary-color;
	border: 1px solid $primary-color !important;
	&:hover {
		color: $white-color;
		background-color: $secondary-color;
	}
}

#rioDashboard {
	background-color: $body-color;
	margin-left: -20px;
	min-height: calc(100vh - 97px);

	h1,
	h2,
	h3,
	h4,
	h5,
	h6,
	p,
	span,
	a,
	button,
	li {
		font-family: "Inter", sans-serif;
	}

	a:focus {
		outline: none;
		box-shadow: none;
	}

	.rv_dash-spacing {
		padding: 40px;
		margin: 40px 0;
	}
	img {
		max-width: 100%;
		height: auto;
	}

	.button-outline {
		border-width: 2px;
		border-style: solid;
		font-weight: 600 !important;
	}

	/* ToggleButton.css */
	.rv_dash-toggle-icon {
		// margin-left: 10px;
		position: absolute;
		top: 38px;
		right: 40px;
		.status-text {
			margin-left: 8px;
			font-weight: 400;
			font-size: 12px;
		}
		.rv_dash-toggle-button {
			position: relative;
			display: inline-block;
			width: 42px;
			height: 24px;
		}

		.rv_dash-toggle-button input[type="checkbox"] {
			display: none;
		}

		.rv_dash-slider {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: transparent;
			border: 1px solid #94a3b8;
			border-radius: 35px;
			cursor: pointer;
			transition: background-color 0.2s;
		}

		.rv_dash-slider::before {
			position: absolute;
			content: "";
			height: 18px;
			width: 18px;
			left: 2px;
			bottom: 2px;
			background-color: #94a3b8;
			border-radius: 35px;
			transition: transform 0.2s;
		}

		input[type="checkbox"]:checked + .rv_dash-slider {
			background-color: #0a9952;
			border: 1px solid #0a9952;
		}

		input[type="checkbox"]:checked + .rv_dash-slider::before {
			transform: translateX(18px);
			background-color: $white-color;
		}

		// disabled
		input[type="checkbox"]:disabled + .rv_dash-slider {
			cursor: no-drop;
		}
	}
	.d-none {
		display: none;
	}

	// badge
	.upcoming-badge {
		font-size: 12px;
		margin-left: 6px;
		background: #01933d;
		color: $white-color;
		padding: 3px 8px;
		font-weight: 400;
		text-transform: capitalize;
		border-radius: 30px;
		display: inline-block;
		vertical-align: text-bottom;
	}
}

