// homepage
.rv_dash-home {
	// welcome banner section
	.welcome-sec {
		background-color: $white-color;
		border-radius: 10px;
		display: grid;
		grid-template-columns: 1fr 1fr;
		column-gap: 80px;
		align-items: center;
		box-shadow: 0 0 15px 0 rgb(15 90 197 / 4%);
		@media (max-width: 768px) {
			grid-template-columns: 1fr;
		}
		.ws-left {
			h1 {
				margin-bottom: 30px;
			}
			p {
				font-size: 15px;
				line-height: 28px;
				margin-bottom: 30px;
				color: $desc-color;
			}
		}
		.ws-right {
			iframe {
				border-radius: 4px;
			}
		}
		iframe {
			width: 100%;
			@media (max-width: 768px) {
				width: 100%;
			}
		}
	}

	// card section
	.rv_dash-card-sec {
		margin: 40px 0px;
		.rv_dash-grid-col-3 {
			display: grid;
			grid-template-columns: repeat(3, 1fr);
			width: 100%;
			gap: 24px;
		}
		.rv_dash-grid-item {
			padding: 40px;
			box-shadow: 0 0 15px 0 rgb(15 90 197 / 4%);
			background-color: $white-color;
			border-radius: 8px;
			&:hover {
				box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
			}
			img {
				width: 40px;
			}
			h5 {
				font-size: 18px;
				font-weight: 500;
				margin-bottom: 12px;
				margin-top: 20px;
			}
			p {
				font-size: 14px;
				color: $desc-color;
				margin-bottom: 20px;
			}
			a {
				color: $primary-color;
				display: inline-flex;
				align-items: center;
				text-underline-offset: 3px;
				padding: 0 !important;
				border: none;
				background-color: transparent;
				font-size: 15px;
				font-weight: 500;

				&:hover {
					color: $secondary-color;
					svg {
						fill: $secondary-color;
					}
				}
				svg {
					width: 11px;
					fill: $primary-color;
				}
			}
		}
	}

	// leave a review
	.leave-a-review {
		text-align: center;
		background: url("https://riovizual.com/wp-content/uploads/2025/02/leave-review-banner.png")
			no-repeat center center;
		background-size: cover;
		border-radius: 10px;
		h3 {
			color: $white-color;
			font-size: 28px;
			font-weight: 700;
			line-height: normal;
			margin: 20px 0px;
		}
		p {
			color: rgba(255, 255, 255, 0.68);
			font-size: 16px;
			font-weight: 400;
			line-height: 30px;
			margin-bottom: 24px;
		}
		.rv-btn-filled {
			background-color: #ffca66 !important;
			color: #191c24 !important;
			font-weight: 600 !important;
			font-size: 15px !important;
			&:hover {
				background-color: #ffa820 !important;
			}
		}
	}
}

// Blocks Page
.rv_dash-blocks-page {
	padding: 60px 0;
	max-width: 70%;
	margin: auto;
	position: relative;

	.rv_dash-block-items {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
		grid-gap: 30px;
		@media (max-width: 768px) {
			grid-template-columns: repeat(1, 1fr);
		}
		.block-item {
			padding: 40px;
			border-radius: 10px;
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			background: $white-color;
			box-shadow: 0px 0px 24px 0px #1018280a;
			border: 1px solid rgba(226, 232, 240, 1);
			position: relative;
			overflow: hidden;
			&:hover {
				box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
			}
			.status-text {
				color: #0f172a;
			}
			.block-item-left {
				flex-basis: 38%;
				.block-item-image {
					position: relative;
				}
			}
			.block-item-right {
				flex-basis: 56%;
				h3 {
					font-size: 20px;
					margin-top: 0;
				}
				p {
					line-height: 1.67;
					font-size: 14px;
					margin-bottom: 26px;
				}
				div {
					a {
						font-size: 14px;
						text-decoration: underline;
						text-underline-offset: 3px;
						color: black;
						font-weight: 500;
						&:first-child {
							margin-right: 18px;
						}
						&:hover {
							color: $secondary-color;
						}
					}
				}
			}
		}
		.item_inactive {
			background: transparent;
			.status-text {
				color: #94a3b8;
			}
		}
		.pro-level-badge {
			background-image: linear-gradient(#fdd835, #fbc02d);
			font-size: 14px;
			text-transform: uppercase;
			font-weight: bold;
			width: 50px;
			height: 50px;
			text-align: center;
			line-height: 50px;
			border-radius: 100%;
			box-shadow: 0px 4px 4px 0px #33333333;
			position: absolute;
			left: 20px;
			top: 20px;
		}
	}
}

// settings page
.rv_dash-settings-page {
	margin: 60px 0;
	label {
		display: block;
		margin-bottom: 4px;
		font-size: 14px;
	}
	input {
		width: 400px;
		padding: 6px 14px;
		border-color: #ddd;
	}
	.rv_dash-lisence-submit-btn {
		margin: 20px 0;
		font-size: 15px;
		padding: 10px 14px;
		border-radius: 6px;
		background-color: #3f6fe2;
		color: $white-color;
		border: none;
		display: inline-block;
		width: auto;
		&:hover {
			cursor: pointer;
		}
	}

	.rv_dash-lisence-box {
		background-color: $white-color;
		box-sizing: border-box;
		max-width: 600px;
		padding: 30px 40px;
		margin: auto;
		border: 1px solid #eee;
		border-radius: 10px;
	}
	.rv_dash-lisence-status {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 10px;
	}
	.rv_dash-lisence-status h5 {
		font-size: 14px;
		margin: 0;
	}
	.rv_dash-lisence-status-btn {
		border: 1px solid #e3e3e3;
		padding: 10px 8px;
		border-radius: 8px;
		font-size: 14px;
		font-weight: 500;
	}
	.rv_dash-lisence-active {
		color: green;
	}
	.rv_dash-lisence-inactive {
		color: #d63638;
	}

	.rv_dash-lisence-inactive-btn {
		background: #ff000096 !important;
	}
	.rv_dash-lisence-active-btn {
		background: #bdbdbd !important;
	}
	.rv_dash-lisence-active-btn:hover {
		background: #878787 !important;
	}

	.rv_dash-lisence-submit-btn {
		svg {
			width: 18px;
			margin-left: 8px;
			vertical-align: middle;
			animation: spin 2s linear infinite;
			// display: none;
		}
	}
	.rv_key_btn_loader {
		cursor: no-drop;
		pointer-events: none;
		// svg{ display: inline-block; }
	}
	@keyframes spin {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}
}

// free vs pro
.rv_dash-free_vs_pro-page {
	padding: 40px 0px;
	.rv_dash-fp-title-section {
		display: flex;
		justify-content: space-between;
		align-items: center;
		h2 {
			font-size: 24px;
		}
	}

	.rv_dash-fp-tabs {
		padding: 30px 0px;
		.components-button {
			background: transparent;
			border: none;
			font-weight: 500;
			font-size: 14px;
			cursor: pointer;
			padding: 0px;
			margin-right: 20px;
			line-height: 3;
			&:hover,
			&.rv-active-tab {
				border-bottom: 1px solid #000000;
			}
		}
		.components-tab-panel__tabs {
			border-bottom: 1px solid #0000001a;
		}
	}
	.rv_dash-fp-content {
		padding: 20px 0px;
		.rv-tb-free-pro-elements {
			table {
				border-radius: 6px;
				background: $white-color;
				width: 100%;
				th,
				td {
					border: 0;
					padding: 13px 32px;
					border-bottom-width: 1px;
					border-bottom-style: solid;
					border-bottom-color: #eee;
					text-align: center;
					color: #64748b;
					font-size: 15px;
					&:first-child {
						text-align: left;
						width: 65%;
					}
				}
				thead {
					background-color: #3f6fe21f;
					th {
						border-bottom-color: #3f6fe2;
						color: #000;
					}
				}
			}
		}
	}
}

//Block activate loader
.rv_dash-loader {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(255, 255, 255, 0.6);
	z-index: 1000;
}

// promotional bar notification
.rv_promo-banner {
	position: relative;
	background: #e9e4ff;
	padding: 12px;
	text-align: center;
	color: #000;
	p {
		margin: 0;
	}
}
